$family:'Cairo-Regular';
@mixin fontFace($family, $src, $style: normal, $weight: normal) {
    @font-face {
        font-family: $family;
        src: url('#{$src}.eot'); // IE9 compat
        src: url('#{$src}.eot?#iefix') format('embedded-opentype'), // IE8 and below
        url('#{$src}.woff') format('woff'), // standards
        url('#{$src}.ttf') format('truetype'), // Safari, Android, iOS
        url('#{$src}.svg##{$family}') format('svg'); // legacy iOS
        font-style: $style;
        font-weight: $weight;
    }
}
@include fontFace('Cairo-Light',
'../fonts/Cairo-Light');
@include fontFace('Cairo-Regular',
'../fonts/Cairo-Regular');
@include fontFace('Cairo-Medium',
'../fonts/Cairo-Medium');
@include fontFace('Almarai-Bold',
'../fonts/Almarai-Bold');
@include fontFace('Butler-Medium',
'../fonts/Butler-Medium');


@include fontFace('Cairo-Light',
'../fonts/Cairo-Light');
@include fontFace('Cairo-Regular',
'../fonts/Cairo-Regular');
@include fontFace('Cairo-Medium',
'../fonts/Cairo-Medium');
@include fontFace('Cairo-Bold',
'../fonts/Cairo-Bold');
@include fontFace('Cairo-SemiBold',
