.bg__sign-in-up  {

    background: url(../images/sign_in_up.png) no-repeat center;
    background-size: cover;
    align-items: center;
    justify-content: center;
    text-align: center;
    h1 {
        color: #fff;
opacity: 0.33;
span {
    font-size: 5rem;
}
    }
}
.logo-form {
    width: 90px;
    margin-bottom: 20px;
}
.left-part {
    height: inherit;

    @media (max-width: 991.9px) {

        display: none;

    }

}


.sign-in-up__logo {
    padding: 20px;
    position: absolute;
    top: 0;
    left: 0;
    width: 90px;
    z-index: 20;

}

.right-part {
    background: $white;

@media (max-width: 991.9px) {

    padding-top: 80px;

}
}


.content-part {
    h4 {
        color: #000;
        font-weight: 500;

        @media (max-width: 575.9px) {

            font-size: 1.6em;
        
        }
    }

    h6 {
        color:  rgba(0, 0, 0, 0.6);
        
        margin-bottom: 1.5em;
    }
}
.overflow-y {
    overflow-y: scroll;
    max-height: 100%;
    @media (max-width:991.9px) {


        overflow-y: hidden;
        max-height: none;
        padding: 0!important;

        
    }
}
.btns__sign-in-ups {
    margin-bottom: 20px;
    text-align: center;

    .btn {
        width: 45px;
        height: 45px;
        margin: 0 5px;
        background: #f5f3f3;
        border-radius: 50%;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        svg {
            transform: scale(.8);
        }

    }
}

.option__span {
    margin-bottom: 2em;

    span {
        color: #000
    }
}

form {
    text-align: left;

    label {
        color: #000;
        font-size: .9em;
        font-family: Cairo-Medium;

    }
    input {
        &::placeholder {
            font-size: .7em;

        }
    }

    .form-text {
        color: rgba(0, 0, 0, .4);
        font-size: .9em;
    }

    .form-check {
        label {
            color: rgb(0, 0, 0);
            font-size: 1em;

        }
    }

    .link-forget {
        a {
            color: $main-color;
            font-size: .9em;
            text-decoration: underline;

        }
    }
}
.signup-link {
    a {
        color:$main-color

    }
}
.log-in {
    .btn {
        width: 90px;
        height: 45px;
        @include border-radius(4px);
        background: $main-color;
        color: #fff;

    }
}

.question {
    margin: 20px 0 20px;
    position: relative;
    text-align: center;

    &:after {
        content: "";
        position: absolute;
        top: calc(50% - 1px);
        left: 0;
        width: 100%;
        height: 1px;
        background: #707070;
        z-index: 1;
    }

    span {
        background: $white;
        padding: 10px;
         color: #8a8a8a;
        z-index: 2;
        font-size: 1.2em;
    position: relative;
    }

}

.sign-up {
    .btn {
        height: 45px;
        @include border-radius(4px);
        color: #fff;
        background: #000;
        width: 145px;

    }

}
.py-up {
    padding-top: 100px;
    padding-bottom: 50px;
    @media (max-width:1199.9px) and (min-width:992px) {


        padding: 30px 0 ;
        
    }
}
