footer {
    background: $dark-blue;
    padding: 70px 0;
    color: #fff;
    font-size: .9em;
    color: #fff;
    font-family: "Cairo-SemiBold";
    position: relative;
.linktop {
    position: absolute;
    top: -50px;
    right: 50px;
    width: 70px;
height: 54px;
border-radius:  4px;
background: #042d53;
display: flex;
justify-content: center;
align-items: center;

}
    .logo {
        margin-bottom: 15px;
        img {
            width: 150px;

        }
    }
    .social__links {
        li {
            margin: 0 4px;
            display: inline-block;
            &:first-child {
                margin-left: 0;
            }

        }
        a {
            display: block;
        }

   
            svg {
                width: 25px;
            }

  

    }
    .title__footer {
        margin-bottom: 15px;
        font-size: 1.3em;
        color: #fff;
    }


    .info__footer {
        span {
            display: flex;
            margin-bottom: 15px;
         
            align-items: center;

            &:first-child {
                align-items: flex-start;
            }


        }

        i {
         
            margin-right: 8px;
            transform: scale(.7);



   

         
        }



    }


    .follow__title {
        font-size: 1.1em;
        margin: 30px 0 15px;
        color: #fff;

    }



    .links {

        li {
            padding: 5px 0;
            margin-bottom: 15px;
        }

        a {
            color: inherit;

        }
    }

}

.col-footer {
    @media (max-width: 1200px) {
        padding: 0 15px;



    }

    &:first-child {
        width: 28%;

        @media (max-width: 991.9px) {

            width: 50% !important;
            margin-bottom: 20px;


        }

        @media (max-width: 575.9px) {

            width: 100% !important;
            padding: 0 10px;


        }

    }

    &:nth-child(2) {
        width: 15%;

        @media (max-width: 991.9px) {

            width: 50% !important;
            margin-bottom: 20px;


        }

        @media (max-width: 575.9px) {

            padding: 0 10px;


        }

    }
    
    &:nth-child(3) {
        width: 32%;

        @media (max-width: 991.9px) {

            width: 50% !important;
            margin-bottom: 20px;


        }

        @media (max-width: 575.9px) {

            padding: 0 10px;


        }

    }
    &:nth-child(4) {
        width: 25%;

        @media (max-width: 1200px) {
            padding: 0;



        }

        @media (max-width: 991.9px) {

            width: 50% !important;
            margin-bottom: 20px;


        }

        @media (max-width: 575.9px) {
            padding: 0 10px;

            width: 100% !important;


        }
    }



}

.form-disc {

    padding: 20px 0;

    input {
        height: 35px;
        width: 195px !important;
        font-size: 1em;
        border: 1px solid #fff;
        border-radius: 5px;
        background: transparent;


        &::placeholder {
            color: #fff;

        }
    }

    button {
        height: 35px;
        width: 80px !important;
        border-radius: 5px;
        background: #fff;
        color: #08345D;
        font-size: .9em;
        border: 0 !important;
        padding: 0;
    }

}

.copy__right {
    margin-top: 45px;
    padding: 20px 0;
    border-top: 1px solid rgba(255, 255, 255, .31);

    .sy-copy {
        font-size: 1.5em;
        margin-right: 5px;

    }
}