.acc-setting {
  background-color: #ececec;
  header {
    position: relative;
    background-color: $white;
    margin-bottom: 2rem;
    
box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);

    .main__bar {
      .menu {
        li {
          color: #1363ac;
        }
      }
      .menu-icon .navicon {
        background: $main-color;
        &:before , &:after {
          background: $main-color;

        }
      }
    }
  }
  .form-area {
    background-color: $white;
    border-radius: 1rem;
    .form-header {
      padding: 1rem 0;
      border-bottom: 1px solid rgba(59, 134, 255, 0.39);
    }
    .user-pic {
      margin: 0 auto;
      width: 115px;
      height: 115px;
      position: relative;
      .add-icon {
        position: absolute;
        right: 0;
        bottom: 1rem;
      }
    }
    .edit-profile,
    .changePassword {
      padding: 3rem 1rem;
      h6 {
        padding-bottom: 0.3rem;
        color: #4d4f5c;
        font-family: Cairo-Regular;
        font-size: 1.1em;
        svg {
          transform: scale(0.8);
        }
      }
      .table-area {
        border: 1px solid rgba(59, 134, 255, 0.39);

        padding: 2rem;
        border-radius: 0.5rem;
        button {
          margin: 0 auto;
          display: block;
          width: 200px;
          height: 45px;
          border-radius: 0.5rem;
          border: 1px solid #3b86ff;
          color: #3b86ff;
          background-color: transparent;
          &.blue-btn {
            color: $white;
            background-color: #3b86ff;
            border: none;
          }
        }
        label {
          font-size: 1em;
          color: #000;
          font-family: "Cairo-SemiBold";
          font-weight: 100;
        }
        input,
        select {
          border: 1px solid rgba(59, 134, 255, 0.39);
          font-size: 18px;
          font-family: "Cairo-SemiBold";
          color: #4d4f5c;
        }
      }
    }
  }
}
.wrapper-trans {
  padding: 3rem 1rem;
  h6 {
    color: #1363ac;
  }
  .btn-outline {
    display: block;
    width: 170px;
    height: 45px;
    line-height: 45px;
    text-align: center;
    border-radius: 0.5rem;
    border: 1px solid #3b86ff;
    color: #3b86ff;
    background-color: transparent;
  }
}
.bootstrap-tagsinput .label-info {
  color: #413f3f;
  background: #ececec;
  display: inline;
  padding: 0.2em 0.6em 0.3em;
  font-size: 75%;
  font-weight: 700;
  line-height: 1;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: 0.25em;
}
.modal {
  .select2-container {
    width: 100% !important;
  }
}
.packages__list {
  .item {
    padding: 0 25px !important;
  }
}

.package-item {
  background: #fff;
  position: relative;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  margin: 0 auto 40px;
  max-width: 325px;
  h5 {
    font-size: 1.6rem;
  }
  &:before {
    content: "";
    color: #fff;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    width: 0;
    height: 0;
    border-top: 180px solid transparent;
    border-bottom: 180px solid transparent;

    border-right: 180px solid #00162a;
  }
  .period {
    position: absolute;
    top: 100px;
    right: -40px;
    padding: 10px 5px;
    background: #1363ac;
    color: #fff;
    z-index: 4;
    font-size: 25px;
    width: 140px;
    &:after {
      content: "";
      position: absolute;
      top: 57px;
      right: 0px;
      z-index: 2;
      width: 0;
      height: 0;
      border-top: 40px solid #074680;
      border-bottom: 40px solid transparent;
      border-right: 40px solid transparent;
    }
  }
  .price {
    position: absolute;
    top: 180px;
    text-align: center;
    right: 15px;
    z-index: 3;
    color: #fff;
    font-size: 19px;
    transform: translateY(-50%);
  }
  .banner__item {
    height: 180px;
    display: flex;
    align-items: center;
    background: #1363ac;
    padding: 0 50% 0 20px;
    h5 {
      color: #fff;
      text-align: left;
    }
  }
  .points__item {
    padding: 100px 15px 20px;
    text-align: left;
    li {
      color: #474747;
      font-size: 14px;
      margin-bottom: 15px;
      svg {
        transform: scale(0.8);
      }
    }
    .btn {
      width: 130px;
      height: 3ee5px;
      background: #1363ac;
      color: #fff;
      border-radius: 0;
    }
  }
  &.item__orange {
    .period {
      background: #e17f36;

      &:after {
        border-top: 40px solid #883f09;
      }
    }

    .banner__item {
      background: #e17f36;
    }
    .points__item {
      .btn {
       
        background: #e17f36;
    
      }
    }
  }
  &.item__green {
    .period {
      background: #2fb994;

      &:after {
        border-top: 40px solid #057859;
      }
    }

    .banner__item {
      background: #2fb994;
    }
    .points__item {
      .btn {
        background: #2fb994;
      }
    }
  }

  &.item__gold {
    &:before {
      border-right-color: #2fb994;
    }
    .period {
      background: #1363ac;

      &:after {
        border-top: 40px solid #074680;
      }
    }

    .banner__item {
      background: linear-gradient(
        #ffb302 0%,
        #ffba01 29.69%,
        #fec300 48.94%,
        #ffcf00 66.52%,
        #fceb85 79.9%,
        #ffdf01 100%
      );
    }
    .points__item {
      .btn {
        background: #e17f36;
      }
    }
  }
  &.item__silver {
    @extend .item__gold;
    .banner__item {
      background: linear-gradient(
        #70706f 0%,
        #7d7d7a 17.99%,
        #8e8d8d 38.08%,
        #a1a2a3 56.89%,
        #d1d1d1 79.9%,
        #bec0c2 100%
      );
    }
  }
  &.item__bronze {
    @extend .item__gold;
    .banner__item {
      background: linear-gradient(
        #836600 0%,
        #836600 21.33%,
        #c1a132 56.89%,
        #fdf198 79.9%,
        #eaca58 100%
      );
    }
  }
}
.contact-packages{
  display: none;
  &.show {display: block;}
  .hint {
    position: relative;
    padding-bottom: 10px;
    margin-bottom: 20px;
    &:after {
      content: "";
      position: absolute;
      width: 83px;
      height: 5px;
      left: 0;
      bottom: 0;
      background: #1363AC;
    }
  }
  .wrapper {
    button {
      width: 235px;
height: 56px;
border-radius: 4px;
background: #bddefc;
font-size: 22px;
    }
  }
}