html {
    font-size: 15px;
}

body {
    font-size: 1rem;
    color: $black;
    background: $white;
    overflow-x: hidden;
    font-family: "Cairo-SemiBold";

}
.fw-reg {
    font-family: "Cairo-Regular";
}
.fw-bold {
    font-family: "Cairo-Bold";

}
 h6 {
    font-family: "Cairo-SemiBold";
}

h1 {
    font-size: 3em;
}

h6,
.h6 {
    font-size: 1.3em;
}

a {

    &:hover {
        text-decoration: none;
    }
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}
.f-med {
    font-family: "Cairo-SemiBold" !important;
}
.mb__section {
    margin-bottom: 7em;

    @media (max-width: 767.9px) {

        margin-bottom: 50px;


    }
}

img {
    max-width: 100%;
}

.h-100vh {
    height: 100vh;
}
.c-blue {
    color: $blue-color;
}
.c-black {
    color: #000 !important;
}
.btn {
    font-size: 1em;

    &.btn-lg {
        padding: .8em 1.1em;
        width: 185px;
        @include border-radius(22px);

    }

    &.btn-small {
        width: 120px;
        padding: .7em 1.1em;
        border-radius: 20px;

    }

    &.btn-green {
        background: $main-color;
        color: $white;
    }

    &.btn-white {
        background: $white;
        color: $black-light;
        border: 1px solid $green-light;
        position: relative;
        transform: perspective(1px) translateZ(0);

        /*&:after {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: $green-dark;
            border: 1px solid $green-dark;
            z-index: -1;
            transform: scale(0);
            @include border-radius(20px);

            transition: all .5s ease-in-out 0s;

            transition-timing-function: ease-out;
        }*/

        &:hover {
            color: $white;

            &:after {
                transform: scale(1);
            }
        }

        &.green-txt-btn {
            border: 1px solid $green-light;
            color: $green-light;

            &:hover {
                color: $white;
            }
        }
    }

    &:focus {
        box-shadow: 0 0 0 0.2rem rgba(98, 183, 150, 0.25);
    }

}

.green-light {
    color: $green-light;
}

.green-dark {
    color: $green-dark;

}

.heading__section {

    h2 {
        font-family: 'Raleway-ExtraLight';
        color: $black;



        span {
            font-family: 'Raleway-Bold';

        }

        @media (max-width: 767.9px) {

            text-align: center;


        }

        @media (max-width: 575.9px) {

            font-size: 2em;
        }
    }

}



#loader-wrapper {
    .loader-section {
        position: fixed;
        top: 0;
        width: 51%;
        height: 100%;
        background: $main-color;
        z-index: 1000;
        -webkit-transform: translateX(0);
        -ms-transform: translateX(0);
        transform: translateX(0);

        &.section-left {
            left: 0;
        }

        &.loader-section.section-right {
            right: 0;
        }
    }
}

.loaded {
    #loader-wrapper {
        .loader-section {
            &.section-left {
                -webkit-transform: translateX(-100%);
                -ms-transform: translateX(-100%);
                transform: translateX(-100%);
                -webkit-transition: all .7s .3s cubic-bezier(.645, .045, .355, 1);
                transition: all .7s .3s cubic-bezier(.645, .045, .355, 1);
            }

            &.section-right {
                -webkit-transform: translateX(100%);
                -ms-transform: translateX(100%);
                transform: translateX(100%);
                -webkit-transition: all .7s .3s cubic-bezier(.645, .045, .355, 1);
                transition: all .7s .3s cubic-bezier(.645, .045, .355, 1);
            }
        }
    }
}


/* Transitions */

.animate {
    -moz-opacity: 0;
    -khtml-opacity: 0;
    -webkit-opacity: 0;
    opacity: 0;
    filter: alpha(opacity=0 * 100);
}

.animated {
    -moz-opacity: 1;
    -khtml-opacity: 1;
    -webkit-opacity: 1;
    opacity: 1;
    filter: alpha(opacity=1 * 100);
}

.scale-up-hor-left {
    -webkit-animation: scale-up-hor-left 0.7s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
    animation: scale-up-hor-left 0.7s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}


