/*===================Checkout==========================*/

.checkout_wrapper {
//     width: 581px;
height: 824px;
border-radius: 19px;
background: #fff;
box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
direction: rtl;
color: #737475;

    .header_checkout {
        background: #EBEBF5;
        padding: 20px;

border-radius: 19px 19px 0px 0px;
font-size: .9em;

        .logo-checkout {
            img {
                width:60px;

            }
        }
    }
    .body_checkout {
        text-align: right;
        padding:50px 20px;
        .fw-bold {
            color: #434547;

        }
    }
    .table-checkout {
      
border-radius: 4px;
background: #fafafc;
border: 1px solid #d1d1d1;

        th {
            color: #737475;
            border-top: 0;

        }
        td {
            color: #242526;
        }
        tr {
            &:last-child {
                td {
                    border: 0;
                }
            }
        }
    }
}
/***** Top content *****/

.top-content { padding: 40px 0 170px 0; }

.top-content .text { color: #fff; }
.top-content .text h1 { color: #fff; }
.top-content .description { margin: 20px 0 10px 0; }
.top-content .description p { opacity: 0.8; }
.top-content .description a { color: #fff; }
.top-content .description a:hover, 
.top-content .description a:focus { border-bottom: 1px dotted #fff; }

.form-box { padding-top: 40px; }

.f1 {
	 background: #fff;
	-moz-border-radius: 4px; -webkit-border-radius: 4px; border-radius: 4px;
    box-shadow: 1px 1px 1px .1px #e2e0e0, -1px -1px 1px .1px #e2e0e0;
    position: relative;
}

.f1 h3 { margin-top: 0; margin-bottom: 5px; text-transform: uppercase; }

.f1-steps {     overflow: hidden;
    position: relative;
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 20px;
    border-bottom: 0.5px solid #9E9E9E;
    padding-bottom: 5px;}


.f1-progress { position: absolute; top: 50px; left: 0; width: 100%; height: 1px; background: #ddd; }
.f1-progress-line { position: absolute; top: 0; left: 0; height: 1px; background: #f35b3f; }

.f1-step { position: relative; float: left; width: 33.333333%; padding: 0 5px; }
.f1-step-icon {
	display: inline-block; width: 40px; height: 40px; margin-top: 4px; background: #ddd;
	font-size: 16px; color: #fff; line-height: 40px;
	-moz-border-radius: 50%; -webkit-border-radius: 50%; border-radius: 50%;
}
.f1-step.activated .f1-step-icon {
	background: #fff; border: 1px solid #f35b3f; color: #f35b3f; line-height: 38px;
}
.f1-step.active .f1-step-icon {
	width: 48px; height: 48px; margin-top: 0; background: #f35b3f; font-size: 22px; line-height: 48px;
}

.f1-step p { color: #ccc; }
.f1-step.activated p { color: #f35b3f; }
.f1-step.active p { color: #f35b3f; }

.f1 fieldset { display: none; text-align: left; }

.f1-buttons { text-align: right; }

.f1 .input-error { border-color: #f35b3f; }

fieldset .form-label
{
    font-size: 20;
    font-weight:400;
    color: #707070;
}
fieldset .first-social
{
    padding-bottom: 20px;
    border: 1px solid #CECDCD;  
    border-top: 0;  
    margin: auto;
}
fieldset .btn-1
{
    display: flex;
    justify-content: space-between;
    border-bottom: 3px solid #2FB994;
    margin-top: 20px;
}
fieldset .btn-1 .social 
{
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    background-color: #F7F4F4;
    height: 60px;
    padding-right: 10px;
    padding-left: 10px;
    text-align: center;
    line-height: 50px;
    margin: 0;
    border: none;
}
fieldset .btn-1 .social
{
    color: #585858;
    font-size: 20px;
    font-weight: 400;
}
fieldset .btn-1 .social:active
{
    background-color: #2FB994;
}
fieldset .social  img
{
    width: 35px;
    height: 35px;
}
fieldset .item-2 h3
{
    font-size: 20px;
    color: #585858;
    font-weight: 400;
    background-color: #F7F4F4;
    height: 60px;
    line-height: 60px;
    text-align: center;
    margin-bottom: 20px;
    margin-top: 30px;
}
fieldset ul.social-2 li
{
    font-size: 20px;
    color: #585858;
    font-weight: 400;
    background-color: #FFFFFF;
    height: 50px;
    text-align: center;
    line-height: 50px;
    border: 1px solid #585858;
    margin-bottom: 3px;
    box-shadow: 0px 4px 6px 0px rgb(0 0 0 / 16%) inset;
    border-radius: 4px;
}
fieldset .item-2 label
{
    font-size: 22px;
    font-weight: medium;
    color: #585858;
    padding-top: 10px;
}
fieldset li .mm:active
{
    background-color: #2FB994;
}
fieldset h4
{
    font-size: 32px;
    font-weight: 400;
    color: #302F2F;
    padding:20px ;
}
input[type="text"], 
input[type="password"], 
textarea, 
textarea.form-control {
	height: 44px;
    margin: 0;
    padding: 0 20px;
    vertical-align: middle;
    background: #fff;
    border: 1px solid #ddd;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    font-weight: 300;
    line-height: 44px;
    color: #888;
    -moz-border-radius: 4px; -webkit-border-radius: 4px; border-radius: 4px;
    -moz-box-shadow: none; -webkit-box-shadow: none; box-shadow: none;
    -o-transition: all .3s; -moz-transition: all .3s; -webkit-transition: all .3s; -ms-transition: all .3s; transition: all .3s;
}

textarea, 
textarea.form-control {
	height: 90px;
	padding-top: 8px;
	padding-bottom: 8px;
	line-height: 30px;
}

input[type="text"]:focus, 
input[type="password"]:focus, 
textarea:focus, 
textarea.form-control:focus {
	outline: 0;
	background: #fff;
    border: 1px solid #ccc;
    -moz-box-shadow: none; -webkit-box-shadow: none; box-shadow: none;
}

input[type="text"]:-moz-placeholder, input[type="password"]:-moz-placeholder, 
textarea:-moz-placeholder, textarea.form-control:-moz-placeholder { color: #888; }

input[type="text"]:-ms-input-placeholder, input[type="password"]:-ms-input-placeholder, 
textarea:-ms-input-placeholder, textarea.form-control:-ms-input-placeholder { color: #888; }

input[type="text"]::-webkit-input-placeholder, input[type="password"]::-webkit-input-placeholder, 
textarea::-webkit-input-placeholder, textarea.form-control::-webkit-input-placeholder { color: #888; }

label { font-weight: 300; }


button.btn {
	min-width: 105px;
	height: 40px;
    margin: 0;
    padding: 0 20px;
    vertical-align: middle;
    border: 0;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    font-weight: 300;
    line-height: 40px;
    color: #fff;
    -moz-border-radius: 4px; -webkit-border-radius: 4px; border-radius: 4px;
    text-shadow: none;
    -moz-box-shadow: none; -webkit-box-shadow: none; box-shadow: none;
    -o-transition: all .3s; -moz-transition: all .3s; -webkit-transition: all .3s; -ms-transition: all .3s; transition: all .3s;
}

button.btn:hover { opacity: 0.6; color: #fff; }
button.btn:active { outline: 0; opacity: 0.6; color: #fff; -moz-box-shadow: none; -webkit-box-shadow: none; box-shadow: none; }
button.btn:focus,
button.btn:active:focus,
button.btn.active:focus { outline: 0; opacity: 0.6; color: #fff; }

button.btn.btn-next,
button.btn.btn-next:focus,
button.btn.btn-next:active:focus, button.btn.btn-next.active:focus { background: #1363AC; }

button.btn.btn-submit,
button.btn.btn-submit:focus,
button.btn.btn-submit:active:focus, button.btn.btn-submit.active:focus { background: #1363AC; }

button.btn.btn-previous,
button.btn.btn-previous:focus,
button.btn.btn-previous:active:focus, button.btn.btn-previous.active:focus { background: #bbb; }

/***** Media queries *****/

@media (min-width: 992px) and (max-width: 1199px) {}

@media (min-width: 768px) and (max-width: 991px) {}

@media (max-width: 767px) {
	
	.navbar { padding-top: 0; }
	.navbar.navbar-no-bg { background: #333; background: rgba(51, 51, 51, 0.9); }
	.navbar-brand { height: 60px; margin-left: 15px; }
	.navbar-collapse { border: 0; }
	.navbar-toggle { margin-top: 12px; }
	
	.top-content { padding: 40px 0 110px 0; }

}

@media (max-width: 415px) {
	
	h1, h2 { font-size: 32px; }
	
	.f1 { padding-bottom: 20px; }
	.f1-buttons button { margin-bottom: 5px; }

}
