
.home__banner{
    height: 100vh;
overflow:hidden;
position: relative;

.img-slide {
    width: 100%;

    overflow: hidden;
    img {
  
        max-width: 100%;
        @media (max-width: 767.9px) {

            height: 100vh;
            max-width: fit-content;
      
      
          }
    }
}
            .slide-content {
                text-align: center;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                z-index: 10;
                .txt-slide {
                    height:100%;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;
                }
                h2, h5 {
                    color: #D8ECFE;
                    margin-bottom: 2rem;
                    span {
                        font-family: "Cairo-SemiBold";
                        font-size: 1.3em;
                    }
                }
             h2 {
                @media (max-width: 991.9px) {

                   font-size: 1.8em;
                    
                    
                    }
                    @media (max-width: 767.9px) {

                        font-size: 1.3em;
                        margin-bottom: 5px;

                         
                         }
             }
             h5 {
                @media (max-width: 991.9px) {

                    font-size: 1.5em;
                     
                     
                     }
                     @media (max-width: 767.9px) {

                        font-size: 1.1em;
                        margin-bottom: 5px;
                         
                         
                         }
             }
                a {
                    height: 55px;
                    width: 250px;
                    background-color: #fff;
                    color: $black;
                    line-height: 55px;
                    text-align: center;
                    margin:  0 auto;
                    display: inline-block;
                    border-radius: 4px;
                    font-size: 20px;
                }
            }
        
    
    .slick-prev{
        background: url(../images/left-arr.svg)no-repeat;
        left: 25px;
        width: 50px;
        height: 50px;
        z-index: 100;

        @media (max-width: 991.9px) {
            display: none!important;
          
          
          }  
  
    }
    .slick-next {
        background: url(../images/right-arr.svg)no-repeat;
 right:25px;
 width: 50px;
 height: 50px;
 z-index: 100;
 @media (max-width: 991.9px) {
  display: none!important;


}
    }
    .hvr-shutter-in-horizontal:before {
        background: #BDDEFC;
    }
}

//--==============================End Main Section ==============================-

// -----========================== Start aboutus__section =========================-
.heading__section {
    margin-bottom: 5rem;
    h2 {
        color: $blue-color;
        font-family: 'Butler-Medium';

    }
    h5 {
        color: #E17F36;
        margin: 0 auto;
        font-family: 'Butler-Medium';

        span {
            color: #2FB994;
        }

    }
}
.aboutus__section {
.description__section {
    @media (max-width: 991.9px) {

      margin: 20px 0;


    }
}
    h5 {
        color: $blue-color;
        font-family: 'Butler-Medium';
        position: relative;
        &:after {
            content:"";
            position: absolute;
            top: 17px;
            left: 65px;
            width: 55px;
            height: 7px;
        }
        &.c-green {
            span {
                color: #2FB994;

            }
            &:after {
                background: #E17F36;
            }
  
        }
        &.c-orange {
            span {
                color: #E17F36;

            }
            &:after {
                background: #2FB994;
            }
        }
    }
    p {
        font-family: 'Cairo-Light';
        font-size: 1.1em;
    }
}

