//--==============================Start Contact Section ==============================
.contact__us {
    .content__section {
        background: url(../images/cover-contact.png) no-repeat;
        background-size: cover;
        background-size: 100% 100%;
        height: 500px;
        display: flex;
        align-items: center;

        h2 {
            color: $white;
            font-family: Cairo-Regular;
        }
        p {
            color: $white;
            width: 80%;
            font-family: 'Cairo-Medium';
            font-size: 1.25rem;
            margin: 0 auto;
            line-height: 2.5rem;
        }
    }
}
.contact__form {
    padding: 50px;
    @media (max-width: 575.9px) {

        padding: 20px;


    }
    .wrapper {
        border-radius: 10px;
background: #fff;
box-shadow: -3px 0px 9px rgba(0, 0, 0, 0.16);
padding: 50px 100px;
@media (max-width: 991.9px) {

    padding: 20px 10px;


}
.map {
    height: 805px;
    iframe {
        height:100%;
    }
    @media (max-width: 991.9px) {

        height: 300px;
    
    
    }
}

.form-contact {
    .form-control {
        border: 1px solid #707070;
        font-size: 1em;
    }
    label {
        font-size: 1em;
        font-family: "Cairo-SemiBold";
    }
    textarea {
        display: block;
        width: 100%;
        resize: none;
        height: 250px;
        margin-bottom: 30px;
        border: 1px solid #707070;
        border-radius: 0.5rem;
        padding: 1rem;
        &:focus-visible, &:focus {
            border-color: #707070;
        }
    }
}
button {
    margin: 0 auto;
    width: 15rem;
    height: 3rem;
    background-color: transparent;
    border: 3px solid #BDDEFC;
    color: $black;
    border-radius: 0.6rem;
    font-weight: bold;
}

    }
}
