
header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    width: 100%;
    transition: all .5s ease-in-out 0s;
    padding: 15px 0;

    .search-icon {
        width: 20px;
        height: 20px;
        display: inline-block;
        background: url(../images/pngs/ico-search.png)no-repeat center;
        background-size: contain;
    }

    .main__bar {
        display: flex;
        justify-content: space-between;
        width: 100%;
        align-items: center;
        flex-direction: row-reverse;

        @media (max-width:991.9px) {
            flex-wrap: wrap;
        }



        .logo {
            width: 85px;
            transition: all .3s ease-out .3s;

            @media (max-width: 767.9px) {
                width: 65px;

            }

            img {
                // height: 100%;
                max-width: 100%;
            }
        }

        .menu-btn {
            display: none;
        }

        .menu-icon {
            cursor: pointer;
            float: right;
            padding: 20px;
            position: relative;
            user-select: none;

            @media (min-width:992px) {
                display: none;
            }

            .navicon {
                background: #fff;

                display: block;
                height: 3px;
                position: relative;
                transition: background .2s ease-out;
                width: 18px;
                @include border-radius(2px);

                &:before {
                    background: #fff;

                    content: '';
                    display: block;
                    height: 100%;
                    position: absolute;
                    transition: all .2s ease-out;
                    width: 50%;
                    top: 7px;
                    right: 0;
                    @include border-radius(2px);
                }

                &:after {
                    background: #fff;
                    content: '';
                    display: block;
                    height: 100%;
                    position: absolute;
                    transition: all .2s ease-out;
                    width: 70%;
                    top: -7px;
                    right: 0;
                    @include border-radius(2px);
                }

                &.dark {
                    background: #707070;

                    &:before {
                        background: #707070;
                    }

                    &:after {
                        background: #707070;
                    }
                }
            }
        }

        .menu {
            ul {
                clear: both;
                max-height: 0;
                overflow: hidden;
                transition: max-height .2s ease-out;
                margin: 10px 0;


                @media (min-width:992px) {
                    clear: none;
                    float: left;
                    max-height: none;
                    height: auto;
                    overflow: visible;
                    display: flex;
                    align-items: center;
                }

                @media (max-width:991.9px) {
                    background: $dark-blue;

                    padding: 0 20px;
                }

            }



            li {
                display: inline-block;
                margin-right: 1.5em;
                position: relative;
                color: $white;
                font-family: "Cairo-SemiBold" !important;
                font-size: 1.1rem;

                @media (max-width:991.9px) {
                    width: 100%;
                    padding: 10px;
                    color: $white;
                    border-bottom: 1px solid rgba(255, 255, 255, .1);
                }
                &.link,.link {
                    position: relative;
                    &:after {
                        content: "";
                        width: 100%;
                        height: 3px;
                        background: #fff;
                        position: absolute;
                        bottom: -4px;
                        left: 0;
                        -webkit-clip-path: inset(0 80% 0 0);
                        clip-path: inset(0 80% 0 0);
                        opacity: 0;
                        -webkit-transition: all 2s;
                        transition: all 2s;
    
                        @media (max-width:991.9px) {
                            display: none;
                        }
                    }
                }

                a {
                    color: inherit;
                &.lang {
                    font-family:"Almarai-Bold";
                    i {
                        margin-left: 8px;
                    }
                }
                }

                &:hover {
                    &:after {
                        opacity: 1;
                        clip-path: inset(0 0 0 0);
                    }
                }
.link {
    &:hover {
        &:after {
            opacity: 1;
            clip-path: inset(0 0 0 0);
        }
    }
}

            }

            .btn {
                @include border-radius(5px);
                width: 120px;
                height: 37px;
                line-height: 29px;
                margin-right: 15px;
                @media (max-width: 1199.9px) {
                    margin-right: 5px;
                }

                &.btn-white {
                    color: #2FB994;
                    background-color: transparent;
                    border: 1px solid #2FB994;
                    &.hvr-sweep-to-right:before  {
                        background: #2FB994;
                    }
                    &:hover {
                        color: $white;
                        border: none;
                    }
                }

                &.btn-green {
                    color: #E17F36;
                    border: 1px solid #E17F36;
                    background-color: transparent;
                    &.hvr-sweep-to-right:before  {
                        background: #E17F36;
                    }
                    &:hover {
                        color: $white;
                        border: none;
                    }
                }

            }
        }
    }

    &.fixed__header {
        background: $dark-blue;
        padding: 10px 0;
        transition: all .5s ease-in-out 0s;
        position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
        .main__bar {
            top: 0;

            .logo {
                height: 50px;
            }
        }
    }

}



header .menu-btn:checked~.menu ul {

    max-height: 80vh;
    overflow-y: scroll;
    overflow-x: hidden;
}

header .menu-btn:checked~.menu-icon .navicon {
    background: transparent;
}

header .menu-btn:checked~.menu-icon .navicon:before {
    transform: rotate(-45deg);
    width: 100%;
}

header .menu-btn:checked~.menu-icon .navicon:after {
    transform: rotate(45deg);
    width: 100%;
}

header .menu-btn:checked~.menu-icon:not(.steps) .navicon:before,
header .menu-btn:checked~.menu-icon:not(.steps) .navicon:after {
    top: 0;
}

header {
    &.h__home {
        li {
            color: #fff;

        }

    }
}

/*===================================DropDown=================================*/

.dropdown-link {
    position: relative;

    &:after {
        content: "";
        position: absolute;
        bottom: -5px;
        width: 100%;
        height: 2px;
        left: 0;
        background: transparent;

        @media (max-width: 991.9px) {
            display: none;
        }
    }



    &:hover {
        &:after {
            background: $green-dark;

        }
    }

    .dropdown-content {
        position: absolute;
        min-width: 170px;
        left: 0px;
        opacity: 0;
        visibility: hidden;
        -moz-transition: opacity 0.2s 0.1s ease-out, visibility 0.1s 0.1s linear;
        -webkit-transition: opacity 0.3s ease-out, visibility 0.1s 0.1s linear;
        -o-transition: opacity 0.3s ease-out, visibility 0.1s 0.1s linear;
        transition: opacity 0.3s ease-out, visibility 0.1s 0.1s linear;

        @media (max-width: 991.9px) {
            position: relative;
            left: auto;
            opacity: 1;
            box-shadow: none;
            visibility: visible;

        }

        a {
            padding: 10px 5px;
            text-decoration: none;
            display: block;
            border-bottom: 1px solid red;
            background: #F0F0F0;
            display: block;
            margin-top: 20px;
            border: 1px solid rgba(235, 235, 235, .8);
            text-align: center;
            width: 100%;
            opacity: 0;
            position: relative;
            font-size: 13px;
            box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.05);
            z-index: 2;
            -moz-transition: opacity 0.1s 0.15s ease-out, margin 0.5s 0.1s ease-out;
            -webkit-transition: opacity 0.1s 0.15s ease-out, margin 0.5s 0.1s ease-out;
            -o-transition: opacity 0.15s 0.15s ease-out, margin 0.5s 0.1s ease-out;
            transition: opacity 0.15s 0.15s ease-out, margin 0.5s 0.1s ease-out;
            margin-bottom: 5px;

            @media (max-width: 991.9px) {
                opacity: 1;
                border-bottom: 0;
                color: $white;
                margin: 0;
                background: none;
                text-align: left;
                border: 0;
                color: #fff;
                box-shadow: none;
            }

            &:last-child {
                border-bottom: 0;
            }

            &:after {
                content: "";
                width: 100%;
                height: 100%;
                background: $green-dark;
                position: absolute;
                top: 0;
                left: 0;
                -webkit-clip-path: inset(0 80% 0 0);
                clip-path: inset(0 80% 0 0);
                opacity: 0;
                -webkit-transition: all 2s;
                transition: all 2s;
                z-index: -1;

                @media (max-width: 991.9px) {
                    display: none;
                }
            }

            &:hover {
                color: #fff;
                transition: color 2s;

                @media (max-width: 991.9px) {
                    color: inherit;
                }

                &:after {
                    clip-path: inset(0 0 0 0);
                    opacity: 1;
                }
            }
        }
    }

    &:hover {
        .dropdown-content {
            opacity: 1;
            visibility: visible;

            a {
                margin-top: 0;
                opacity: 1;

                &:first-child {
                    margin-top: 15px;

                    @media (max-width: 991.9px) {
                        margin-top: 0;


                    }
                }
            }
        }
    }
}

/**** social navbar *******/
.menu-item,
.menu-open-button {
  background: #1592E6;
  border-radius: 100%;
  width: 40px;
  height: 40px;
  position: absolute;
  top: 50%;
  color: white;
  text-align: center;
  line-height: 40px;
  transform: translate3d(0, 0, 0);
  transition: transform ease-out 200ms;
}

.menu-open {
  display: none;
}

.hamburger {
  width: 19px;
  height: 2px;
  background: white;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -9.5px;
  margin-top: -1.5px;
  transition: transform 200ms;
}

.hamburger-1 {
  transform: translate3d(0, -8px, 0);
}

.hamburger-2 {
  transform: translate3d(0, 0, 0);
}

.hamburger-3 {
  transform: translate3d(0, 8px, 0);
}

.menu-open:checked + .menu-open-button .hamburger-1 {
  transform: translate3d(0, 0, 0) rotate(45deg);
}

.menu-open:checked + .menu-open-button .hamburger-2 {
  transform: translate3d(0, 0, 0) scale(0.1, 1);
}

.menu-open:checked + .menu-open-button .hamburger-3 {
  transform: translate3d(0, 0, 0) rotate(-45deg);
}

.social-menu {
  position: absolute;
  right: 1rem;
  top: 15rem;
  padding-top: 0;
  padding-left: 0;
  width: 55px;
  height: 150px;
  box-sizing: border-box;
  font-size: 20px;
  text-align: left;
  @media (max-width: 991.9px) {

   top: auto;
   bottom: 80px;
     
     
     }
}

.menu-item:hover {
  background: white;
  color: #00bcd4;
  transition: all .3s;
}

.menu-item:nth-child(3) {
  transition-duration: 180ms;
}

.menu-item:nth-child(4) {
  transition-duration: 180ms;
}

.menu-item:nth-child(5) {
  transition-duration: 180ms;
  img {
    width: 34px;

  }
}

.menu-item:nth-child(6) {
  transition-duration: 180ms;
}

.menu-open-button {
  z-index: 2;
  transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition-duration: 400ms;
  transform: scale(1.1, 1.1) translate3d(0, 0, 0);
  cursor: pointer;
}

.menu-open-button:hover {
  transform: scale(1.2, 1.2) translate3d(0, 0, 0);
}

.menu-open:checked + .menu-open-button {
  transition-timing-function: linear;
  transition-duration: 200ms;
  transform: scale(0.8, 0.8) translate3d(0, 0, 0);
}

.menu-open:checked ~ .menu-item {
  transition-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1);
}

.menu-open:checked ~ .menu-item:nth-child(3) {
  transition-duration: 190ms;
  transform: translate3d(0, -45px, 0);
  background-color: transparent;
}

.menu-open:checked ~ .menu-item:nth-child(4) {
  transition-duration: 290ms;
  transform: translate3d(0, -95px, 0);
  background-color: transparent;
}

.menu-open:checked ~ .menu-item:nth-child(5) {
  transition-duration: 390ms;
  transform: translate3d(0, -145px, 0);
  background-color: transparent;


}

.menu-open:checked ~ .menu-item:nth-child(6) {
  transition-duration: 490ms;
  transform: translate3d(0, -350px, 0);
}

